/*Style the navigation bar */
.navbar {
  background-color: transparent;
  padding: 10px 5px;
}
.navbar-nav {
  display: flex;
  align-items: center;
}

.nav-item {
  margin: 5px;
  padding: 5px;
}
.nav-link {
  border: 1px solid #ff0000;
  border-radius: 7px;
  color: #ff0000;
  background-color: #00000091;
  display: inline-block;

  padding: 14px 0px;
  text-align: center;
  text-decoration: none;
  width: 170px;

  font-weight: 600;
  font-size: 1.2rem;
}

/* Change color on hover */
.nav-link:hover {
  border: 1px solid #ffffff;
  border-radius: 15px;
  transition: 500ms;
  background-color: #111111d5;
  color: #ffffff;
}
.active {
    background-color: #ff000035;
   
  }
.active:hover {
  border: 1px solid #ffffff;
  border-radius: 15px;
  transition: 500ms;
  background-color: #111111d5;
  color: #ffffff;
}

@media screen and (max-width: 600px) {
  .navbar {
    padding: 25px;
  }
  .navbar-nav {
    flex-direction: column;
    width: 100%;
  }
  .nav-item {
    float: none;
    width: inherit;
  }
  .nav-link {
    width: inherit;
  }
}
