.services-header {
    /*background-image: url("your-image-url.jpg");*/
    background-size: cover;
    background-position: center;
    position: relative;
  }
  
  .services-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.5;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .services-title {
    font-size: 6rem;
    color: white;
    text-shadow: 2px 2px 5px black;
    opacity: 0.1;
    text-transform: uppercase;
    letter-spacing: 0.5rem;
  }
  
  .services-content {
    position: relative;
    z-index: 1;
    padding: 2rem;
    text-align: center;
  }
  
  .services-subtitle {
    font-size: 3rem;
    margin-bottom: 1rem;
    text-transform: uppercase;
    letter-spacing: 0.5rem;
  }
  /*body of service component*/
  .services-section {
    background-color: aliceblue;
    padding: 15px 25px; 
    margin: 5px;
  }
  .services-section:hover {
    background-color: #000000;
    color: #ff0000;
    transition: 500ms;
    animation: backInLeft;
 }
  .services-button {
    border: none;
    background-color: azure;
    width: 100%;
  }